import { InjectionToken } from '@angular/core';

export interface AuthConfig { 
  tokenUrl: string;
  loginUrl: string;
  refreshUrl?: string;
  loginUrlParamRedirect: string;
  loginUrlParamChallenge: string;
  loginUrlParamNonce: string;
  logoutUrl?: string;
  logoutUrlParamIdToken?: string;
  logoutUrlParamRedirect?: string;
  requiredRole?: string;
  tokenPrefix?: string;
}

export const AUTH_CONFIG = new InjectionToken<AuthConfig>(undefined);

export const INTERCEPTOR_BASE_PATHS = new InjectionToken<string[]>(undefined);