import { Observable, BehaviorSubject } from 'rxjs';
import { User } from '../user.model';
export abstract class AvlAuthService {
  /**
   * User data is held in this property
   */
  protected userSubject = new BehaviorSubject<User>(undefined);

  /**
   * Current user as Observable
   */
  public user$: Observable<User> = this.userSubject.asObservable();

  /**
   * String holding error message in case user couldn't authenticate
   */
  protected errorMessageSubject = new BehaviorSubject<string>(undefined);

  /**
   * Error messsage in case failed authentication as Observable
   */
  public errorMessage$: Observable<string> = this.errorMessageSubject.asObservable();

  /**
   * Observable tracking authenticated state
   * Resolves to true on successful log in, false otherwise
   */
  public abstract isLoggedIn$: Observable<boolean>;

  /**
   * Observable tracking if redirected from idb
   * Resolves to true on redirection, false otherwise
   */
  public abstract redirectedFromLogIn: BehaviorSubject<boolean>;

  /**
   * Login user and redirect after successful authentication.
   * Custom auth services need to provide implementation
   * @param redirectUrl Url to redirect after succesful authentication
   */
  public abstract login(redirectUrl?: string): void;

  /**
  * If tokens are peristed in local storage, set current user data
  */
  public abstract reloadPersistedUserData(): void;

  /**
   * Log out user and redirect after.
   * Custom auth services need to provide implementation
   * @param redirectUrl Url to redirect after logging out
   */
  public abstract logout(redirectUrl?: string): void;

  /**
   * Currently authenticated user data
   */
  public get currentUser(): User {
    return this.userSubject.value;
  }

  /**
   * Current error message for fail authentication
   * Undefined when user is logged in
   */
  public get errorMessage(): string {
    return this.errorMessageSubject.value;
  }

  /**
   * Use to clear error message after user is informed
   */
  public clearErrorMessage() {
    this.errorMessageSubject.next(undefined);
  }

  /**
   * For token based authetication.
   * Provide functionality to refresh tokens
   */
  public refreshTokens?(): Promise<any>;

  /**
   * For token based authetication.
   * Updates user authentication with refresh tokens
   */
   public abstract refreshUserIfNeeded(): void;

  /**
  * Read user from session storage
  */
   public abstract readUserFromSessionStorage(): User;

  /**
  * Store user to session storage
  */
   public abstract storeUserToSessionStorage(user: User): void;
   
  /**
  * Clear user from session storage
  */
   public abstract clearUserFromSessionStorage(): void;
}
